
.Admin {
  flex: 1;
  width:calc(100% - 1rem);
  padding: 0.5rem;
  background: var(--bg-color);
  color: var(--text-color);

  .HomeBtnPannel {
    width:100%;
  }

  .LoginForm {
    width: 100%;
    max-width: 400px;
    gap: 1rem;

    .LoginInput {
      width: 100%;
      padding: 0.7rem 1rem;
      font-size: 1rem;
      border: 1px solid #ccc;
      border-radius: 0.5rem;
      background: var(--bg-color);
      color: var(--text-color);
      &:focus {
        outline: none;
        border-color: #61dafb;
      }
    }

    .LoginButton {
      width: 100%;
      padding: 0.8rem 1rem;
      font-size: 1rem;
      font-weight: bold;
      cursor: pointer;
      &:hover {
        background: #0a0;
      }
    }
  }
}
